@font-face {
  font-family: 'Noto Sans SC';
  font-weight: 400;
  src: url('/src/fonts/Chivo-Regular.ttf');
}
.f_h1 {
  color: #000000;
  font-family: 'Noto Sans SC', sans-serif;
  font-size: 68px;
  line-height: 70px;
  text-transform: uppercase;
}
@media screen and (max-width: 1440px) {
  .f_h1 {
    font-size: 50px;
    line-height: 60px;
  }
}
@media screen and (max-width: 767px) {
  .f_h1 {
    font-size: 40px;
    line-height: 45px;
  }
}
.f_h2 {
  color: #000000;
  font-family: 'Noto Sans SC', sans-serif;
  font-size: 60px;
  line-height: 65px;
  text-transform: uppercase;
}
.f_h3 {
  color: #000000;
  font-family: 'Noto Sans SC', sans-serif;
  font-size: 44px;
  line-height: 50px;
  text-transform: uppercase;
}
.f_h4 {
  color: #000000;
  font-family: 'Noto Sans SC', sans-serif;
  font-size: 24px;
  line-height: 24px;
}
.f_h5 {
  color: #000000;
  font-family: 'Noto Sans SC', sans-serif;
  font-size: 14px;
  line-height: 48px;
  font-weight: 600;
}
.f_h6 {
  color: #000000;
  font-family: 'Noto Sans SC', sans-serif;
  font-size: 24px;
  line-height: 32px;
  font-weight: 700;
  text-transform: capitalize;
}
.f_p {
  color: #000000;
  font-family: 'Noto Sans SC', sans-serif;
  font-size: 16px;
  line-height: 26px;
}
* {
  box-sizing: border-box;
}
#root {
  height: 100%;
  width: 100%;
}
a {
  text-decoration: none;
}
body {
  height: 100vh;
  margin: 0;
  overflow: hidden;
}
button {
  background: none;
  border: none;
  outline: none;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
html {
  scroll-behavior: smooth;
}
input {
  background: none;
  border: none;
}
input:focus {
  outline: none;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
